<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">新闻详情</span>
      </div>
      <div class="content-wrapper">
        这里是新闻详情
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import {getQuery} from 'common/js/util'

  export default {
    data () {
      return {

      }
    },
    created () {
      this._getDetail()
    },
    methods: {
      _getDetail(){
        let id = getQuery('id')
        if(!id){
          return
        }
      },
      back() {
        this.$router.back()
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active, .slide-leave-active
    transition all 0.3s

  .slide-enter, .slide-leave-to
    transform translate3d(100%, 0, 0)

  .wrapper
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    background $color-background
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      z-index 1
      background $color-background
      display flex
      justify-content center

</style>



